import { IKDataEntity } from 'metaflow-js'
import hillo from 'hillo'

const entity = {
  id: {
    type: IKDataEntity.Types.Integer,
    displayName: 'id',
    form: false,
  },
  firstName: {
    displayName: 'Vorname',
  },
  lastName: {
    displayName: 'Nachname',
  },
  address: {
    displayName: 'Addresse',
  },
  eatTime: {
    type: IKDataEntity.Types.Time,
    displayName: 'Essenszeit',
  },
  phone: {
    displayName: 'Telephone',
  },
}
const entityName = 'corona'
const config = {
  add: function (item) {
    return hillo.post(`Route.php?op=${entityName}add`, item, {
      showLoading: true,
    })
  },
  edit: function (item) {
    return hillo.post(`Route.php?op=${entityName}update`, item, {
      showLoading: true,
    })
  },
  remove: function (id) {
    return hillo.post(`Route.php?op=${entityName}delete`, { id: id })
  },
  load: async function (filter) {
    return (await hillo.get(`Route.php?op=${entityName}list`, {
      ...filter,
    }))
      .content
  },
}

export default IKDataEntity.ModelFactory(entity, config)
